body {
    font-family: 'Noto Sans KR';
    font-weight: 600;
    color: #333;
}
a:link {
    color: #333;
    text-decoration: none; }

a:visited {
    color: inherit;
    text-decoration: none !important;
}
a:hover {
    color: #337ab7;
}

.header {
    background-color: white;
    font-size: 18px;
    color: #333;
    display: flex;
    justify-content: space-between;
    height: 50px;
    border-bottom: 1px solid #c1c1c1;
}
.header__elem1 {
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header__elem1__logo {
    display: flex;
}
.header__elem1__logo:first-child {
    padding-right: 10px;
    box-sizing: border-box;
}

.header__elem1__link {
    padding: 10px 15px 10px 15px;
    border-bottom: 2px solid rgba(193, 193, 193, 0);
}
.header__elem1__link.--selected {
    color: #337ab7 !important;
    border-bottom: 2px solid #337ab7;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
.nav-vertical-bar {
    height: 50%;
    border-right: 1px solid #c1c1c1 !important;
}
.header__elem2 {
    font-size: 15px;
    font-style: italic;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
